<template>
  <div class="container">
    <app-header></app-header>
    <div class="row mt-3">
      <div class="col-md-3"></div>
      <div class="col-md-6">
        <div class="card">
          <div class="card-body">
            <img src="../../src/assets/timg.jpg" alt="" class="mx-auto d-block">
            <form action="" @submit.prevent="onBy">
              <div class="form-group">
                <label for="email">邮箱</label>
                <input type="email" class="form-control" v-model="email">
              </div>
              <div class="form-group">
                <label for="password">密码</label>
                <input type="password" class="form-control" v-model="password">
              </div>

              <button class="btn btn-block btn-success" type="submit">登录</button>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "register",
    data(){
      return{
        email:'',
        password:'',
      }
    },
    //组件内守卫
    beforeRouteEnter:(to, from, next) =>{
      next(vm => vm.$store.dispatch('setUser',null))
    },
    methods:{
      onSubmit(){
        fetch("https://qesudhhp.api.lncld.net/1.1/classes/user?fetchWhenSave=true", {
          method: 'get',
          headers: {
            "X-LC-Id": "QESUdHhpIfmS3TCUFUTEGgG6-gzGzoHsz",
            "X-LC-Key": "w92x47dUeGm80tVX9C3dYwyG",
            "Content-Type": "application/json;charset=utf-8"
          },
        })
          .then(res => {
            return res.json()
          })
          .then(data =>{
            const users = []
            const results = data.results
            for(let key in results){
              let user = results[key]
              users.push(user)
            }
            //过滤
            let temp = users.filter((user)=>{
              return user.email === this.email && user.password === this.password
            })

            //console.log(temp)
            if(temp != null && temp.length >0 ){
              this.$store.dispatch("setUser",temp[0].email)
              this.$router.push({name:'homeLink'})
            }else {
              alert('密码错误')
              this.$store.dispatch('setUser',null)
            }
          })
      },
      onBy(){
            if (this.email == '') {
              alert('请输入邮箱')
            } else if (this.password == '') {
              alert('请输入密码')
            } else {
              this.onSubmit()
            }
      }
    }
  }
</script>

<style scoped>

</style>



