<template>
  <div class="container">
    <app-header></app-header>
    <!--菜单-->
    <div class="row">
      <div class="col-sm-12 col-md-8">
        <table class="table">
          <thead class="thead-default">
          <tr>
            <th>尺寸</th>
            <th>价格</th>
            <th>加入</th>
          </tr>
          </thead>
          <tbody v-for="item in getMenuItems" :key="getMenuItems.name">
          <tr>
            <td><strong>{{item.name}}</strong></td>
          </tr>
          <tr v-for="option in item.options" :key="option.size">
            <td>{{option.size}}</td>
            <td>{{option.price}}</td>
            <td><button
              @click="addToBasket(item, option)"
              class="btn btn-sm btn-outline-success">+</button></td>
          </tr>
          </tbody>
        </table>
      </div>

      <!--购物车-->
      <div class="col-sm-12 col-md-4">
          <table class="table">
            <thead class="thead-default">
              <tr>
                <th>数量</th>
                <th>品种</th>
                <th>价格</th>
              </tr>
            </thead>
            <tbody v-for="(list,index) in baskets" v-show="baskets.length">
              <tr>
                <td>
                  <button class="btn btn-sm" style="background: none;" @click="addorLessSun(list,false,index)">-</button>
                  <span>{{list.quantity}}</span>
                  <button class="btn btn-sm" style="background: none;" @click="addorLessSun(list,true,index)">+</button>
                </td>
                <td>{{list.name}}{{list.size}}</td>
                <td>{{list.price * list.quantity}}</td>
              </tr>
            </tbody>
          </table>
          <p v-show="baskets.length">总价 : {{total}}</p>
          <button class="btn-success btn-block" v-show="baskets.length">提交</button>
          <p v-show="baskets.length == 0">购物车暂无商品...</p>
      </div>
    </div>

  </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: "menu1",
        data(){
          return{
            baskets:[],
            //getMenuItems:{}
          }
        },
        created(){
          this.fetchData()
        },
        computed:{
          //计算总价
          total(){
           let totalCost = 0

           for(let index in this.baskets){
             let individualItem = this.baskets[index]
             totalCost += individualItem.quantity * individualItem.price
           }

           return totalCost + ' ' + 'RMB'
          },
          //在vuex中获取数据
          getMenuItems(){
            //return this.$store.state.menuItems
            return this.$store.getters.getMenuItems
          }
        },
        methods:{
          fetchData(){
            fetch("https://qesudhhp.api.lncld.net/1.1/classes/menu",{
              method:'get',
              headers:{
                "X-LC-Id": "QESUdHhpIfmS3TCUFUTEGgG6-gzGzoHsz" ,
                "X-LC-Key": "w92x47dUeGm80tVX9C3dYwyG" ,
                "Content-Type": "application/json;charset=utf-8"
              }
            })
              .then(res => {
                return res.json()
              })
              .then(data => {
                let pizzaArray = data.results;
                this.$store.commit('setMenuItems',pizzaArray);

              })

          },
          addToBasket(item, option){
              let basket = {
                name:item.name,
                size:option.size,
                price:option.price,
                quantity:1
              }
              if(this.baskets.length > 0){
                    //过滤
                    let result = this.baskets.filter((isbasket)=>{
                      return (isbasket.name === item.name && isbasket.price === option.price)
                    })
                //console.log(result);
                if(result != null && result.length > 0 ){
                  result[0].quantity++;
                }else{
                  this.baskets.push(basket)
                }

              }else {
                this.baskets.push(basket)
              }

          },

          addorLessSun(list,flag,index){
              if(flag){
                list.quantity +=1;
              }else{
                list.quantity -=1;
                if(list.quantity <= 0){
                  this.delShop(index);
                  //console.log(this.tempindex)
                }
              }
          },
          delShop(index){
            this.baskets.splice(index,1)
          }
        }
    }
</script>

<style scoped>

</style>



