
import Vue from 'vue'
import Vuex from 'vuex'

import * as actions from './actions'       //引入actions.js里面的所有
import * as getters from './getters'        //引入getters.js里面的所有
import * as mutations from './mutations'    //引入mutations.js里面的所有
Vue.use(Vuex)


export const store = new Vuex.Store({
  state:{
    //设置属性
    menuItems:{},
    currentUser:null,
    isLogin:false
  },
  getters,        //执行getters.js里面的东西
  mutations,      //执行mutations.js里面的东西
  actions         //执行actions.js里面的东西
})



