<template>
  <div class="container">
    <app-header></app-header>
    <div class="row">
      <div class="col-sm-12 col-md-8">
        <app-new-pizza></app-new-pizza>
      </div>
      <div class="col-sm-12 col-md-4">
          <!--品种展示-->
        <h3 class="text-muted my-5">菜单</h3>
        <table class="table">
          <thead class="table-default">
          <tr>
            <th>品种</th>
            <th>删除</th>
          </tr>
          </thead>
          <tbody v-for="item in getMenuItems" :key="item.name">
          <tr>
            <td>{{item.name}}</td>
            <td>
              <button class="btn btn-outline-danger btn-sm" @click="deldata(item)">&times;</button>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
  import newPizza from './NewPizza'
    export default {
        name: "admin",
        data(){
          return{
            //getMenuItems:[]
          }
        },
      components:{
          'app-new-pizza':newPizza
      },
      computed:{
          getMenuItems(){
            return this.$store.getters.getMenuItems
          }
       /* getMenuItems:{
          get(){
            //return this.$store.state.menuItems
            return this.$store.getters.getMenuItems
          },
          set(){

          }
        }*/
      },
      created(){
          fetch("https://qesudhhp.api.lncld.net/1.1/classes/menu",{
            method:'get',
            headers:{
              "X-LC-Id": "QESUdHhpIfmS3TCUFUTEGgG6-gzGzoHsz" ,
              "X-LC-Key": "w92x47dUeGm80tVX9C3dYwyG" ,
              "Content-Type": "application/json;charset=utf-8"
            }
          })
            .then(res => {
              return res.json()
            })
            .then(data => {
              let pizzaArray = data.results;
              this.$store.commit('setMenuItems',pizzaArray)   //请求下来的数据同步到vuex
            })
      },
      methods:{
        deldata(item){
          fetch("https://qesudhhp.api.lncld.net/1.1/classes/menu/"+item.objectId,{
            method:'DELETE',
            headers:{
              "X-LC-Id": "QESUdHhpIfmS3TCUFUTEGgG6-gzGzoHsz" ,
              "X-LC-Key": "w92x47dUeGm80tVX9C3dYwyG" ,
              "Content-Type": "application/json;charset=utf-8"
            }
          })
            .then(res => res.json())
            .then(data => {
              this.$store.commit('removeMenuItems',item)         //删除的数据同步到vuex
            })
            .catch(err => console.log(err))
        }
      }

    }
</script>

<style scoped>

</style>



