<template>
  <div class="container">
    <app-header></app-header>
    <div class="row mt-3">
      <div class="col-md-3"></div>
      <div class="col-md-6">
        <div class="card">
          <div class="card-body">
            <img src="../../src/assets/logo.png" alt="" class="mx-auto d-block">
            <form action="" @submit.prevent="formBy">
              <div class="form-group">
                <label for="email">邮箱</label>
                <input type="email" class="form-control" v-model="email">
              </div>
              <div class="form-group">
                <label for="password">密码</label>
                <input type="password" class="form-control" v-model="password">
              </div>
              <div class="form-group">
                <label for="confirm-password">确认密码</label>
                <input type="password" class="form-control" v-model="confirmPassword">
              </div>
              <button class="btn btn-block btn-success" type="submit">注册</button>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
      name: "register",
      data() {
        return {
          email: '',
          password: '',
          confirmPassword: ''
        }
      },
      methods: {
        onSubmit() {
          const userData = {
            email: this.email,
            password: this.password,
          }

          fetch("https://qesudhhp.api.lncld.net/1.1/classes/user?fetchWhenSave=true", {
            method: 'post',
            headers: {
              "X-LC-Id": "QESUdHhpIfmS3TCUFUTEGgG6-gzGzoHsz",
              "X-LC-Key": "w92x47dUeGm80tVX9C3dYwyG",
              "Content-Type": "application/json;charset=utf-8"
            },
            body: JSON.stringify(userData)
          })
            .then(res => this.$router.push({name: 'loginLink'}))
        },

        formBy() {
          if (this.email == '') {
            alert('请输入邮箱')
          } else if (this.password == '' && this.confirmPassword == '') {
            alert('请输入密码')
          } else if (this.password != this.confirmPassword) {
            alert('两次输入密码不一样')
          } else {
            this.onSubmit()
          }
        }

      }
    }
</script>

<style scoped>

</style>



